import * as React from 'react';

import Highcharts from 'react-highcharts'; 
import HighchartsMore from 'highcharts-more';

// HighchartsMore(Highcharts.Highcharts)

const ROOT_CLASS = 'dashboard-income';

export class DashboardIncome extends React.Component{
	constructor() {
		super();
		
		this.state = {
			activebaseAsset: 'USD',
			activeTypeShow: 'baseIncome',
		};
	}

	render() {
		const { myIncomeFollowings } = this.props;
		const { activebaseAsset, activeTypeShow } = this.state;

		let showIncomes = Object.keys(myIncomeFollowings)
			.filter(keyIncomeArr => !!myIncomeFollowings[keyIncomeArr][activebaseAsset])
			.map(keyIncomeArr => 
				({
					name: `Key-${keyIncomeArr}`,
					data: myIncomeFollowings[keyIncomeArr][activebaseAsset]['baseBalance']
						.filter((cur, index, arr) => (
							(cur.value !== 0) && (cur.timestamp >= myIncomeFollowings[keyIncomeArr][activebaseAsset].createdAt)
						))
						.map((cur, index, arr) => (
							[new Date(cur.timestamp).getTime(), (
								activeTypeShow === 'baseBalance' ? (cur.value - arr[0].value) : 
									(((cur.value - arr[0].value) / arr[0].value) * 100)
							)]
						)),
				}));

		let nowProfit = showIncomes
			.reduce((pr, cur, index) => pr + ((cur.data && cur.data.length && cur.data[cur.data.length-1][1]) || 0), 0);

		let generalData = showIncomes
			.reduce((pr, cur) => pr.concat([cur.data.map(cur2 => cur2[1])]), [])
			.reduce((pr, cur) => pr.concat(cur), []);

		console.log({
			myIncomeFollowings
		});

	  return (
	    <div className={`${ROOT_CLASS}`}>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text`}>Your profit (out of following):</div>
	    		<div className={`__button ${activeTypeShow==='baseIncome'}`} onClick={()=>this.setState({activeTypeShow:'baseIncome'})}>in %</div>
	    		<div className={`__button ${activeTypeShow==='baseBalance'}`} onClick={()=>this.setState({activeTypeShow:'baseBalance'})}>in asset</div>
	    		<div className={`__text`}>for:</div>
	    		<div className={`__button ${activebaseAsset==='USD'}`} onClick={()=>this.setState({activebaseAsset:'USD'})}>USD</div>
	    		<div className={`__button ${activebaseAsset==='BTC'}`} onClick={()=>this.setState({activebaseAsset:'BTC'})}>BTC</div>
	    	</div>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text allotted`}>Now (total): {`${nowProfit}${activeTypeShow==='baseBalance'?activebaseAsset:'%'}`}</div>
	    	</div>
	    	<div className={`${ROOT_CLASS}__head`}>
	    		<div className={`__text`}>History:</div>
	    	</div>
	    	<div>
	    		{
	    			(showIncomes.length > 0) &&
		    			<Highcharts
		            config={{
		              chart: {
		                type: 'area',
		                backgroundColor: 'rgba(0,0,0,0)',
		              },
		              title: {
		                text: '',
		              },
		              subtitle: {
		                text: document.ontouchstart === undefined ?
		                    'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in',
		              },
		              xAxis: {
		                type: 'datetime',
		                allowDecimals: false,
		              },
		              yAxis: {
		                title: {
		                  text: activeTypeShow === 'baseBalance' ? activebaseAsset : '%',
		                },
		                showFirstLabel: false,
		                showLastLabel: true,
		                min: Math.min(...generalData),
		                max: Math.max(...generalData),
		              },
		              rangeSelector: {
		                selected: 1,
		              },
		              legend: {
		                enabled: true,
		              },
		              credits: {
		                enabled: false,
		              },
		              plotOptions: {
		                area: {
		                  marker: {
		                    radius: 2,
		                  },
		                },
		              },
		              series: [...showIncomes],
		            }}
		          />
	      	}
	    	</div>
	    </div>
    )
	 }
};



